<template>
  <div id="navMain">
  <div :style="{position:position}"  class="container-fluid navBar">
      <div class="row">
        <div class="col-lg-12 navF col-md-12 col-sm-12 col-12">
          <div class="left-side">
            <router-link to="/"><img src="images/logo.png"/></router-link>
          </div>
          <ul class="center">
            <li><router-link to="/">Home</router-link></li>
            <li><router-link to="/shop">Shop</router-link></li>
            <li><router-link to="/about">About</router-link></li>
            <li id="drop" @mousemove="move=true" @mouseleave="move=false">
              Pages
            <section id="dropDown" v-show="move">
              <router-link to="/productDetail">Product Detail</router-link>
              <router-link to="/cart">Cart</router-link>
              <router-link to="/checkout">Checkout</router-link>
            </section>
            </li>
          </ul>
          <div class="right-side">
            <section id="inp" v-show="show">
              <input type="text" placeholder="shoes">
                <font-awesome-icon id="icon" :icon="['fas', 'magnifying-glass']" />
            </section>
            <font-awesome-icon @click="show=!show" id="icon" :icon="['fas', 'magnifying-glass']" />
            <router-link :style="{color:'white',textDecoration:'none'}" to="/cart">
              <div id="itemCount">{{itemCount}}</div>
              <font-awesome-icon id="icon"  :icon="['fas', 'cart-shopping']" />
            </router-link>
            <router-link :style="{color:'white',textDecoration:'none'}" to="/favourite">
              <div id="itemCount">{{itemCount2}}</div>
              <font-awesome-icon id="icon" :icon="['fas', 'heart']" />
            </router-link>

          </div>
          <div class="menu">
            <font-awesome-icon @click="show=!show" :icon="['fas', 'bars']" />
            <ul v-show="show" class="center">
              <li><router-link to="/">Home</router-link></li>
              <li><router-link to="/shop">Shop</router-link></li>
              <li><router-link to="/about">About</router-link></li>
              <li id="drop" @mousemove="move=true" @mouseleave="move=false">
                Pages
                <section id="dropDown" v-show="move">
                  <router-link to="/productDetail">Product Detail</router-link>
                  <router-link to="/cart">Cart</router-link>
                  <router-link to="/checkout">Checkout</router-link>
                  <router-link to="/favourite">Favourite</router-link>
                </section>
              </li>
            </ul>
          </div>
        </div>
      </div>
  </div>
  </div>
 <div :style="{bottom:`${scroll}px`}" id="bot">
   <div id="goTop" @click="scrollToTop">
     <font-awesome-icon :icon="['fas', 'chevron-up']" />
   </div>
   <div v-if="open" id="chat">
     <p @click="open=false" id="tp">-</p>
     <section id="f">
       <img src="images/user2.png"/>
       <h2>Julie Smith</h2>
     </section>
     <div id="form" v-if="chat" >
       <form @submit.prevent="handleSubmit">
       <input type="text" placeholder="Name" required v-model="name">
       <input type="email" placeholder="Email" required>
       <button >START CHAT</button>
         <h1>We are always here to help our customers</h1>
       </form>
     </div>
      <div class="main" v-else>
          <h4>Can we help you <br/> {{name}}</h4>
        <div id="chatBoard"></div>
        <section id="down">
          <input type="text" placeholder="Type Something ..." v-model="text">
          <button @click="handleSend"><font-awesome-icon :icon="['fas', 'paper-plane']" /></button>
        </section>
      </div>
   </div>
   <img v-else  @click="open=true" src="images/chat.png"/>

 </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
export default {
  name:'NavbarComponent',
  data(){
    return {
      show: false,
      move:false,
      position:'static',
      open:false,
      name:'',
      chat:true,
      text:'',
      scroll:-100,
      itemCount:null,
      itemCount2:null
    }
  },
  components:{FontAwesomeIcon},
  created() {
    library.add(fas)
    this.getItems()
  },
  mounted() {
    window.addEventListener('scroll',this.handleScroll)
    console.log(this.position)
  },
  methods:{
    getItems() {
      const array = JSON.parse(localStorage.getItem('key'));
      let quantity = 0;
      const array2=JSON.parse(localStorage.getItem('favourite'))
      let quantity2 = 0;

      if (array) {
        array.forEach((item) => {
          quantity += item.quantity || 0;
        });
      }
      if (array2) {
        array2.forEach((item) => {
          quantity2 += item.quantity || 0;
        });
      }
      this.itemCount2 = quantity2;

      this.itemCount = quantity;
    },
    handleScroll(){
      if(window.scrollY>=250){
        this.position='fixed'
        this.scroll=50
      }else{
        this.position='static'
        this.scroll=-100
      }
    },
    scrollToTop(){
      window.scrollTo(0,0)
    },
    handleSubmit(){
      this.chat=false
    },
    handleSend(){
      var container=document.getElementById('chatBoard')
     var text= document.createElement('div')
      text.className='div'
      container.appendChild(text)
      const user=document.createElement('img')
      user.src='images/user.png'
      text.appendChild(user)
      const nxt=document.createElement('section')
      text.appendChild(nxt)
      const chat=document.createElement('span')
      chat.innerHTML=this.text
      nxt.appendChild(chat)
      if(text){
          text.scrollIntoView({ behavior: 'smooth' });
      }
    }
  },

}
</script>

<style scoped>

</style>