<template>
  <div>
    <div class="container-fluid shop">
      <div class="row">
        <div class="col-lg-1 r col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-11 r col-md-11 col-sm-11 col-11">
          <h1>My Favourite</h1>
        </div>
      </div>
    </div>

    <div class="container-fluid products">
      <div class="row">
        <div class="col-lg-1 prdct col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-10 prdct col-md-10 col-sm-10 col-10">
          <div class="wrap" id="wrap"  >
            <div data-aos="fade-up" class="product-card" id="pro" v-for="arrival in favourite" :key="arrival.id">
              <button id="gia" @click="()=>removeItem(arrival.id)">
                <font-awesome-icon :icon="['fas', 'xmark']" />
              </button>

              <section id="shoeBox">
                <div id="ok">
                  <section @click="handleProduct(arrival)"><font-awesome-icon :icon="['far', 'eye']" /></section>
                  <section><font-awesome-icon :icon="['fas', 'heart']" /></section>
                </div>
                <img :src="arrival.imgSrc">
                <div class="littleBox">${{arrival.productPrice}}</div>
              </section>
              <h2>{{arrival.productName}}</h2>
              <button @click="handleCart(arrival)">Add To Cart</button>
            </div>
          </div>
        </div>
        <div class="col-lg-1 prdct col-md-1 col-sm-1 col-1"></div>
      </div>
    </div>


  </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
export default {
  name: "FavouriteComponent",
  data() {
    return {
      favourite: JSON.parse(localStorage.getItem('favourite')) || [],

    }
  },
  created() {
    library.add(fas)

  },
  components:{
    FontAwesomeIcon
  },
  methods: {
    handleCart(item) {
      const existingItemIndex = this.array.findIndex(item2 => item2.id === item.id);
      //ete existingItemIndex chi gtnum nuyn idov item returna anum -1
      if (existingItemIndex !== -1) {
        this.array[existingItemIndex].quantity += 1;
      } else {
        item.quantity = 1;
        this.array.push(item);
      }
      


      // Update the localStorage
      localStorage.setItem('key', JSON.stringify(this.array));
    },
    handleProduct(item) {
      this.$router.push('/productDetail')
      localStorage.setItem('product', JSON.stringify(item))
      window.scrollTo(0, 300)

    },
    removeItem(id) {
      console.log(id)
      this.favourite = this.favourite.filter(item => item.id !== id);
      
      if (this.favourite.length===0){
        localStorage.removeItem('favourite')
      }
      localStorage.setItem('favourite', JSON.stringify(this.favourite))
    }
  }
}

</script>

