<template>
  <div>
    <div class="container-fluid shop">
      <div class="row">
        <div class="col-lg-1 r col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-11 r col-md-11 col-sm-11 col-11">
          <h1>Shop</h1>
        </div>
      </div>
    </div>
    <div class="container-fluid filter-shop">
      <div class="row">
        <div class="col-lg-1 filter col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-2 filter col-md-2 col-sm-3 col-3">
          <div class="filteR">
            <section class="lm">
              <input type="text" @input="handleInput" placeholder="Search For Shoes ..." v-model="valueOfProduct">
              <font-awesome-icon id="lo" :icon="['fas', 'magnifying-glass']" />
            </section>
            <section class="lm">
              <h3>Categories</h3>
              <ul>
                <li @click="handleType('')"  :style="{color: type === '' ? 'lightgreen' : 'grey'}">All</li>
                <li @click="handleType('Classics')"  :style="{color: type === 'Classics' ? 'lightgreen' : 'grey'}">Classics</li>
                <li @click="handleType('Modern')"  :style="{ color: type === 'Modern' ? 'lightgreen' : 'grey'}">Modern</li>
                <li @click="handleType('Sports')"  :style="{ color: type === 'Sports' ? 'lightgreen' : 'grey'}">Sports</li>
                <li @click="handleType('Comfort')"  :style="{color: type === 'Comfort' ? 'lightgreen' : 'grey'}">Comfort</li>
              </ul>
            </section>
            <section class="lm">
              <h3>By Color</h3>
              <h4><section @click="handleColor('')" :style="{background:'url(https://png.pngtree.com/png-vector/20230529/ourmid/pngtree-circle-prohibited-sign-with-red-gradation-color-vector-png-image_7111879.png)', outline: color === '' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> All</h4>
              <h4><section @click="handleColor('black')" :style="{background:'black', outline: color === 'black' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Black</h4>
              <h4><section @click="handleColor('red')" :style="{background:'red', outline: color === 'red' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Red</h4>
              <h4><section @click="handleColor('grey')" :style="{background:'grey', outline: color === 'grey' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Grey</h4>
              <h4><section @click="handleColor('blue')" :style="{background:'blue', outline: color === 'blue' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Blue</h4>
              <h4><section @click="handleColor('yellow')" :style="{background:'yellow', outline: color === 'yellow' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Yellow</h4>
              <h4><section @click="handleColor('white')" :style="{background:'white', outline: color === 'white' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> White</h4>
              <h4><section @click="handleColor('brown')" :style="{background:'brown', outline: color === 'brown' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Brown</h4>
              <h4><section @click="handleColor('multi')" :style="{background:'url(https://i.pinimg.com/originals/4c/44/b8/4c44b8de8a859a119a84512df8b506b2.jpg)', outline: color === 'multi' ? '1px solid #DEB380' : 'none'}" id="lkp"></section> Multi</h4>
            </section>
            <section class="lm">
              <h3>Availability</h3>
              <h4><section id="kl3" @click="handleStock('')" :style="{outline: stock === '' ? '1px solid #DEB380' : 'none'}" ></section> All</h4>
              <h4><section id="kl" @click="handleStock('in')" :style="{outline: stock === 'in' ? '1px solid #DEB380' : 'none'}"></section>In Stock </h4>
              <h4><section id="kl1" @click="handleStock('out')" :style="{outline: stock === 'out' ? '1px solid #DEB380' : 'none'}"></section>Out Of Stock </h4>
            </section>
          </div>
        </div>
        <div class="col-lg-8 shop5 col-md-8 col-sm-7 col-7">

        <div class="shopItems">
          <div data-aos="fade-up"  v-for="arrival in paginatedItems" :key="arrival.id"   class="product-card">
            <section  class="kop" id="shoeBox">
              <div id="ok">
                <section @click="handleProduct(arrival)"><font-awesome-icon :icon="['far', 'eye']" /></section>
                <section @click="handleFavourite(arrival)"><font-awesome-icon :icon="['fas', 'heart']" /></section>
              </div>
              <img :src="arrival.imgSrc">
              <div class="bigBox1" v-if="arrival.stock==='in'">Is Available</div>
              <div class="bigBox2" v-else>Out Of Stock</div>
              <div class="littleBox">${{arrival.productPrice}}</div>
            </section>
            <h2>{{arrival.productName}}</h2>
            <button @click="handleCart(arrival)">Add To Cart</button>
          </div>
        </div>

          <vue-awesome-pagination
              :total-items="9"
              :items-per-page="itemsPerPage"
              :max-pages-shown="3"
              v-model="currentPage"
          />
          <div id="pagination">
            <button @click="goToPreviousPage" >
              <font-awesome-icon :icon="['fas', 'chevron-left']" />
            </button>
            <template v-for="pageNumber in totalPages" :key="pageNumber">
              <button @click="goToPage(pageNumber)"  :class="pageNumber===currentPage?'active':''">{{ pageNumber }}</button>
            </template>
            <button @click="goToNextPage" >
              <font-awesome-icon :icon="['fas', 'chevron-right']" />
            </button>
          </div>
        </div>
        <div class="col-lg-1 shop5 col-md-1 col-sm-1 col-1"></div>
      </div>
    </div>
    <div class="container-fluid brand">
      <div class="row">
        <div class="col-lg-1 mo col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-11 mo col-md-11 col-sm-11 col-11">
          <h1>COMING OUT SOON!</h1>
          <h2>NEW <span>BROGUE</span> COLLECTION</h2>
        </div>
      </div>
      <div class="row">
        <div class="col-lg-1 mo1 col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-10 mo1 col-md-10 col-sm-10 col-10">
          <img src="images/shoe2.png">
        </div>
        <div class="col-lg-1 mo1 col-md-1 col-sm-1 col-1"></div>
      </div>
    </div>

  </div>
</template>

<script>
import data from '../data/data.json'
import VueAwesomePagination from 'vue-awesome-paginate';
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import { fas } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import { far } from '@fortawesome/free-regular-svg-icons';
import AOS from 'aos'

AOS.init({duration:1500})
export default {
  name:'ShopComponent',
  data(){
    return{
      dataArray: [...data.arrivals,...data.array1, ...data.array2, ...data.array3], // Combine arrays as needed
      currentPage: 1,
      itemsPerPage: 9,
      color:'',
      stock:'',
      type:'',
      valueOfProduct:'',
      array:JSON.parse(localStorage.getItem('key')) || []
    }
  },
  created() {
    library.add(fas,far)

  },
  components: {
    VueAwesomePagination,
    FontAwesomeIcon
  },
  methods:{
    handleFavourite(item){
      const existingItemIndex = this.pp.findIndex(item2 => item2.id === item.id);
      //ete existingItemIndex chi gtnum nuyn idov item returna anum -1
      if (existingItemIndex !== -1) {
        return;
      } else {
        this.pp.push(item);
      }
      


      // Update the localStorage
      localStorage.setItem('favourite', JSON.stringify(this.pp));
    },

    handleProduct(item) {
      this.$router.push('/productDetail')
      localStorage.setItem('product', JSON.stringify(item))
      window.scrollTo(0,300)
    },
    handleInput(ev){
      this.valueOfProduct=ev.target.value.toLowerCase()
    },
    handleCart(item){
       const existingItemIndex = this.array.findIndex(item2 => item2.id === item.id);
      //ete existingItemIndex chi gtnum nuyn idov item returna anum -1
      if (existingItemIndex !== -1) {
        this.array[existingItemIndex].quantity += 1;
        this.array[existingItemIndex].total+=item.productPrice

      } else {
        item.quantity = 1;
        this.array.push(item);
      }
      
      // Update the localStorage
      localStorage.setItem('key', JSON.stringify(this.array));
    },
    goToPreviousPage() {
      if (this.currentPage > 1) {
        this.currentPage -= 1;
      }
    },
    goToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage += 1;
      }
    },
    goToPage(pageNumber) {
      this.currentPage = pageNumber;
    },
    handleColor(color){
      this.color=color
    },
    handleStock(stock){
        this.stock=stock
    },
    handleType(type){
      this.type=type
    }

  },
  computed: {
    paginatedItems() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.filteredItems.slice(startIndex, endIndex) || [];
    },
    totalPages() {
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    },
      filteredItems() {
        if (this.color === '' && this.stock === '' && this.type === '' && this.valueOfProduct==='') {
          return this.dataArray;
        } else {
          const lowerCaseSearch = this.valueOfProduct.toLowerCase();
          return this.dataArray.filter((item) => {
            return (this.color === '' || item.color === this.color) &&
                (this.stock === '' || item.stock === this.stock) &&
                (this.type === '' || item.type === this.type) &&
                (this.valueOfProduct === '' || item.productName.toLowerCase().includes(lowerCaseSearch))
          })
        }
      }}
}
</script>

<style scoped>

</style>

