              <li :class="recievedObject.size===41?'active':''" @click="recievedObject.size=41">41</li>
              <li :class="recievedObject.size===42?'active':''" @click="recievedObject.size=42">42</li>
              <li :class="recievedObject.size===43?'active':''" @click="recievedObject.size=43">43</li>
              <li :class="recievedObject.size===44?'active':''" @click="recievedObject.size=44">44</li>
              <li :class="recievedObject.size===45?'active':''" @click="recievedObject.size=45">45</li>
            </ul>

            <h2>
              Color:
            </h2>
            <div class="y">
              <section  :style="{background:recievedObject.color !=='multi'?recievedObject.color:`url(&quot;https://i.pinimg.com/originals/4c/44/b8/4c44b8de8a859a119a84512df8b506b2.jpg&quot;)`}" id="lkd"></section>
              <span>{{recievedObject.color}}</span>
            </div>
            <div id="qanak">
              <button  @click="(recievedObject.quantity>1 ?recievedObject.quantity-=1:null) && (recievedObject.total-=recievedObject.productPrice)">-</button>
              <input type="button" :value="recievedObject.quantity">
              <button @click="(recievedObject.quantity+=1 ) && (recievedObject.total+=recievedObject.productPrice)">+</button>
            </div>
            <section id="mine">
              <button @click="handleCart(recievedObject)" id="l1">Add To Cart</button>
              <button id="l2">Purchase Now</button>
            </section>
          </div>
          <div class="col-lg-1 rt col-md-1 col-sm-1 col-1"></div>
        </div>
      </div>
    <div class="container-fluid reviews">
      <div class="row">
        <div class="col-lg-1 fk col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-10 fk col-md-10 col-sm-10 col-10">
         <section id="y">
           <h1 @click="turner=true" :class="turner===true?'activec':''">Reviews(36)</h1>
           <h1 @click="turner=false" :class="turner===false?'activec':''">Product Detail</h1>
         </section>
          <div v-if="turner" class="review-part">
            <div class="revS">
              <section id="of">
                <section class="ko">
                  <img src="images/nkr1.png">
                  <h3>Jeager Milka</h3>
                </section>
                <p>
                  Cobbler's shoes are a work of art. Each pair is meticulously crafted with precision and passion. I am constantly impressed by the unique designs and the level of comfort.
                </p>
              </section>
              <section id="of">
                <section class="ko">
                  <img src="images/nkr2.png">
                  <h3>Fresco Miller</h3>
                </section>
                <p>
                  I cannot express how impressed I am with Cobbler's customer service. They went above and beyond to ensure I got the right pair of shoes that suited my style.
                </p>
              </section>
              <section id="of">
                <section class="ko">
                  <img src="images/nkr3.png">
                  <h3>Dave Walker</h3>
                </section>
                <p>
                  Cobbler's shoes are the epitome of style and elegance. I receive compliments every time I wear them. The craftsmanship is outstanding, and the fit is perfect.
                </p>
              </section>
              <div id="bottom" @click="load=!load">
                <p v-if="load">Load More Reviews <font-awesome-icon :icon="['fas', 'chevron-up']" /></p>
                <p v-else>Load Less Reviews <font-awesome-icon :icon="['fas', 'chevron-down']" /></p>
              </div>
              <section v-if="load" id="of">
                <section class="ko">
                  <img src="images/nkr1.png">
                  <h3>Jeager Milka</h3>
                </section>
                <p>
                  Cobbler's shoes are a work of art. Each pair is meticulously crafted with precision and passion. I am constantly impressed by the unique designs and the level of comfort.
                </p>
              </section>
              <section v-if="load" id="of">
                <section class="ko">
                  <img src="images/nkr2.png">
                  <h3>Fresco Miller</h3>
                </section>
                <p>
                  I cannot express how impressed I am with Cobbler's customer service. They went above and beyond to ensure I got the right pair of shoes that suited my style.
                </p>
              </section>
            </div>
            <div class="rightPart">
                <section>
                  <h1>Add a Review</h1>
                  <form @submit="(ev)=>handleSubmit(ev)">
                    <input type="text" placeholder="Name" v-model="model.name" required>
                    <input type="email" placeholder="Email" v-model="model.email"  required>
                    <textarea placeholder="Review" v-model="model.review" required></textarea>
                    <button>Add A Review</button>
                  </form>
                </section>
            </div>

          </div>
          <div v-else class="detailOf">
            <p>Introducing the Brogue Shoe: Classic Elegance and Timeless Style. Step into sophistication with our exquisite Brogue shoe. Crafted with meticulous attention to detail, this iconic footwear combines traditional craftsmanship with modern flair.</p>
            <h2>Key Features:</h2>
            <ul>
              <li>
                Premium Quality: Made from genuine leather, the Brogue shoe offers unparalleled durability and comfort. Its sturdy construction ensures long-lasting wear, making it a reliable companion for any occasion.
              </li>
              <li>
                Classic Design: The Brogue shoe showcases a timeless design with its distinctive perforated detailing and wingtip pattern. Its sleek and polished appearance adds a touch of elegance to your ensemble.
              </li>
              <li>
                Versatile Colors: Choose from our range of versatile color options, including rich brown, sleek black, and sophisticated oxblood. Each shade complements various outfits, allowing you to express your personal style.
              </li>
            </ul>
            <h2>Experience the Perfect Fit:</h2>
            <ul>
              <li>Customized Comfort: The Brogue shoe features a cushioned insole and a flexible outsole, providing optimal comfort throughout the day. It molds to the contours of your feet, ensuring a comfortable fit with every step.</li>
              <li>Secure Closure: Equipped with a lace-up closure, the Brogue shoe offers a secure and adjustable fit. You can tighten or loosen the laces according to your preference, providing added convenience and support.</li>
            </ul>
            <p>
              Whether you're attending a formal event, heading to the office, or simply stepping out in style, the Brogue shoe is a timeless choice that exudes confidence and refinement. Elevate your footwear collection with this classic masterpiece.
            </p>
            <p>
              Please note: The actual color of the product may vary slightly from the images due to lighting and monitor settings.
            </p>
          </div>
        </div>
        <div class="col-lg-1 fk col-md-1 col-sm-1 col-1"></div>
      </div>
    </div>
    <div id="sa" class="container-fluid slider">
      <div class="row">
        <div class="col-lg-1 vg col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-11 vg col-md-11 col-sm-11 col-11">
          <h1>Related Products</h1>
        </div>
      </div>
      <div class="row">
        <div class="col-lg-12 vg2 col-md-12 col-sm-12 col-12">
          <swiper  :autoplay="{
      delay:3000,
      disableOnInteraction: false,
    }"  :modules="modules" :slidesPerView="slideNum" :loop="true"  :spaceBetween="30">
            <swiper-slide v-for="arrival in arrivals" :key="arrival.id" >
              <div data-aos="fade-up" class="product-card">
                <section id="shoeBox">
                  <div id="ok">
                    <section @click="handleProduct(arrival)"><font-awesome-icon :icon="['far', 'eye']" /></section>
                    <section @click="handleFavourite(arrival)"><font-awesome-icon :icon="['fas', 'heart']" /></section>

                  </div>
                  <img :src="arrival.imgSrc">
                  <div class="littleBox">${{arrival.productPrice}}</div>
                </section>
                <h2>{{arrival.productName}}</h2>
                <button @click="handleCart(arrival)">Add To Cart</button>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { far } from '@fortawesome/free-regular-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import 'swiper/swiper-bundle.css';
import { Autoplay } from 'swiper';
import 'swiper/css';
import data from "@/data/data.json";
import _ from 'lodash'
export default {
  name:'ProductDetail',
  created() {
    library.add(fas,far)
    this.slideNumY()
    window.addEventListener('resize', this.slideNumY);
  },
  components:{
    FontAwesomeIcon,
    Swiper,
    SwiperSlide
  },
  data(){
    return{
      arrivals:data.arrivals,
      slideNum:6,
      load:false,
      modules:[Autoplay],
      recievedObject:JSON.parse(localStorage.getItem('product')) ||
       {
        id:2.0,
        imgSrc:"https://cobbler.uiparadox.co.uk/assets/media/products/pd-1.png",
        productName: "Brogue",
        productPrice: 368,
        type:"Modern",
        description:"The Brogue features a distinctive perforated design on the upper, known as broguing, which adds a touch of refinement and chara-ter to the wearer of the shoepair.",
        material: "leather",
        size:35,
        quantity:1,
        color:'brown',
        total:368
      },
      turner:true,
      model:{
        name:'',
        review:'',
        email:''
      },
      array:JSON.parse(localStorage.getItem('key')) || []

    }
  },
  beforeUnmount() {
    sessionStorage.removeItem('product')
  },
  methods:{
    handleFavourite(item){
      const existingItemIndex = this.pp.findIndex(item2 => item2.id === item.id);
      //ete existingItemIndex chi gtnum nuyn idov item returna anum -1
      if (existingItemIndex !== -1) {
        return;
      } else {
        this.pp.push(item);
      }
      


      // Update the localStorage
      localStorage.setItem('favourite', JSON.stringify(this.pp));
    },
    slideNumY(){
      console.log(window.innerWidth)
      if(window.innerWidth<=1400 && window.innerWidth>=1200){
        this.slideNum=6
      }
      if(window.innerWidth<=1199 && window.innerWidth>=992){
        this.slideNum=5
      }
      if(window.innerWidth<=991 && window.innerWidth>=768){
        this.slideNum=4
      }
      if(window.innerWidth<=767 && window.innerWidth>=576){
        this.slideNum=3
      }
      if(window.innerWidth<=576 && window.innerWidth>=0) {
        this.slideNum=2
      }
    },
    handleCart(item){
      const existingItemIndex = this.array.findIndex(item2 => item2.id === item.id);
      //ete existingItemIndex chi gtnum nuyn idov item returna anum -1
      if (existingItemIndex !== -1) {
        this.array[existingItemIndex].quantity += item.quantity;
      } else {
        // item.quantity = 1;
        this.array.push(item);
      }
      


      localStorage.setItem('key', JSON.stringify(this.array));
    },
    handleProduct(item) {
      localStorage.setItem('product', JSON.stringify(item));
      window.scrollTo(0,300)
      this.$router.push('/productDetail')
    },
    handleSubmit(ev){
      ev.preventDefault();
      const outerSection = document.createElement('section');
      outerSection.id = 'of';

      const innerSection = document.createElement('section');
      innerSection.classList.add('ko');

      const image = document.createElement('img');
      const arrayOfImages=['images/nkr1.png','images/nkr2.png','images/nkr3.png']
      image.src = arrayOfImages[_.random(0,arrayOfImages.length-1)];

      const heading = document.createElement('h3');
      heading.textContent = this.model.name;

      innerSection.appendChild(image);
      innerSection.appendChild(heading);

      const paragraph = document.createElement('p');
      paragraph.textContent = this.model.review

      outerSection.appendChild(innerSection);
      outerSection.appendChild(paragraph);

      document.getElementsByClassName('revS')[0].appendChild(outerSection);
      this.model.name=''
      this.model.review=''
      this.model.email=''
    }

  }
}
</script>

