<template>
  <div>
    <div class="container-fluid shop">
      <div class="row">
        <div class="col-lg-1 r col-md-1 col-sm-1 col-1"></div>
        <div class="col-lg-11 r col-md-11 col-sm-11 col-11">
          <h1>Checkout</h1>
        </div>
      </div>
    </div>
        <div class="container-fluid cart">
          <div class="row">
            <div class="col-lg-1 vb col-md-1 col-sm-1 col-1"></div>
            <div class="col-lg-10 vb col-md-10 col-sm-10 col-10">
              <h1>Cart Items</h1>
            </div>
            <div class="col-lg-1 vb col-md-1 col-sm-1 col-1"></div>
          </div>
          <div class="row">
            <div class="col-lg-1 vo col-md-1 col-sm-1 col-1"></div>
            <div id="ui" class="col-lg-7 vo col-md-7 col-sm-7 col-7">
              <div v-for="item in localCart"  class="cart-product" :key="item.id">
                <button id="lho" @click="()=>removeItem(item.id)"><font-awesome-icon :icon="['far', 'trash-can']" /></button>

                <div id="lft">
                  <img :src="item.imgSrc"/>
                </div>
                <div id="rght">
                  <section id="tpo">
                    <h3>Item:<span>{{item.productName}}</span></h3>
                    <div class="lop">
                      <h3>Price</h3>
                      <h3>Quantity</h3>
                      <h3>Sub Total</h3>
                    </div>
                  </section>
                  <section id="bto">
                    <div class="bg">
                      <h3>Color</h3>
                      <section id="saw"  :style="{background:item.color !=='multi'?item.color:`url(&quot;https://i.pinimg.com/originals/4c/44/b8/4c44b8de8a859a119a84512df8b506b2.jpg&quot;)`}">
                      </section>
                      <h3 id="fde">Sizes</h3>
                      <section  class="lkdf" >
                        <p>{{item.size}}</p>
                      </section>
                    </div>
                    <div class="mnb">
                      <h3>${{item.productPrice}}.00</h3>
                      <div id="qanak">
                        <button  @click="(item.quantity>1 ?item.quantity-=1:null) && (item.total-=item.productPrice)">-</button>
                        <input type="button" :value="item.quantity">
                        <button @click="(item.quantity+=1 ) && (item.total+=item.productPrice)">+</button>
                      </div>
                      <h3 id="o">${{item.total}}.00</h3>
                    </div>
                  </section>
                </div>
              </div>
              <div class="gfd">
              </div>
            </div>
            <div id="uy" class="col-lg-3 vo col-md-3 col-sm-3 col-3">
              <div class="cart-total">
                <section>
                  <h2>Order Detail</h2>
                  <div id="lb">
                    <h4>Items Total</h4>
                    <b>${{subTotal}}.00</b>
                  </div>
                  <div id="lb">
                    <h4>Shipping Fee</h4>
                    <b>$05.00</b>
                  </div>
                  <div id="lb">
                    <h4>Tax</h4>
                    <b>$02.90</b>
                  </div>
                  <div id="lb">
                    <h4>Discount</h4>
                    <b>$20.00</b>
                  </div>
                </section>
                <section id="yt">
                  <p>Payment Method</p>
                  <label id="sae">
                    <input @click="acc=true"  value="cashOnDelivery" checked="checked"   type="radio" name="id01">
                    <span>Cash on Delivery</span>
                  </label>
                 <label>
                   <input @click="acc=false"   value="paymentByCard"  type="radio" name="id01">
                   <span>Payment By Card</span>
                 </label>
                </section>
                <section>
                  <div class="pak">
                    <p>Grand Total</p>
                    <h5>${{grandTotal}}0</h5>
                  </div>
                  <button @click="handleEnd">Place Order Now</button>
                </section>
              </div>
            </div>
            <div class="col-lg-1 vo col-md-1 col-sm-1 col-1"></div>
          </div>
          <div class="row">
            <div class="col-lg-3 gjd col-md-3 col-sm-3 col-3"></div>
            <div class="col-lg-6 gjd col-md-6 col-sm-6 col-6">
              <div v-if="acc" class="cash">
                <label>
                  Your Adress
                  <br><input type="text" placeholder="Oxford strt. 25"/>
                </label>
                <label>
                  Currency
                 <br> <select>
                    <option>Choose one</option>
                    <option>USD</option>
                    <option>AMD</option>
                    <option>RUB</option>
                  </select>
                </label>
              </div>
              <div v-else id="credit">
                <img src="https://seeklogo.com/images/V/VISA-logo-62D5B26FE1-seeklogo.com.png"/>
                <h4>Card Number</h4>
                <section id="nums">
                  <input type="text" v-model="a" @input="kop" placeholder="XXXX XXXX XXXX XXXX">
                </section>
                <section id="aloo">
                  <label id="op1">
                    <h4>Card Holder</h4>
                    <input type="text" id="dfd"  placeholder="Name Surname">
                  </label>
                  <label id="op2">
                    <h4>Exp. Date</h4>
                    <input type="text" v-model="mod" @input="modd"   placeholder="10/26">
                  </label>
                  <label id="op3">
                    <h4>CVV</h4>
                    <input type="text" @input="limit" v-model="inp"   placeholder="123">
                  </label>
                </section>
              </div>

            </div>
            <div class="col-lg-3 gjd col-md-3 col-sm-3 col-3"></div>
          </div>
        </div>
    <div v-if="ok" id="order">
      <img src="https://i.pinimg.com/originals/74/fb/9d/74fb9d629f3d7dd509a67e9a9b721f05.gif">
    </div>
    <div v-else></div>

  </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { far } from '@fortawesome/free-regular-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
export default {
  name:'CheckoutComponent',
  data(){
    return{
      localCart:JSON.parse(localStorage.getItem('key')) || [
        {
          color: "brown",
          description: "Elevate your casual look with these Felted Sneakers, a timeless classic crafted with rubber soles.",
          id: 53,
          imgSrc: "https://cobbler.uiparadox.co.uk/assets/media/products/p-3.png",
          material: "rubber",
          productName: "Felted Sneakers",
          productPrice: 69,
          quantity: 3,
          size: 39,
          stock: "in",
          total:207,
          type: "Classics",
        },
        {
          color: "blue",
          description: "Experience unparalleled comfort with these Running Trainers featuring a foam construction.",
          id: 51,
          imgSrc: "https://cobbler.uiparadox.co.uk/assets/media/products/p-1.png",
          material: "foam",
          productName: "Running Trainers",
          productPrice: 58,
          quantity: 1,
          size: 39,
          stock: "in",
          total: 58,
          type: "Comfort"
        },
        {
          color: "yellow",
          description: "Stay ahead of the trend with these Modern Running Trainers featuring a sleek design and synthetic materials.",
          id: 56,
          imgSrc: "https://cobbler.uiparadox.co.uk/assets/media/products/p-6.png",
          material: "synthetics",
          productName: "Running Trainers",
          productPrice: 66,
          quantity: 2,
          size: 39,
          stock: "in",
          total: 132,
          type: "Modern"
        }
      ],
      subTotal:0,
      grandTotal:0,
      selected: 'cashOnDelivery',
      acc:true,
      inp:'',
      mod:'',
      a:'',
      ok:false
    }
  },
  created() {
    this.calculateTotals()
    library.add(fas,far)

  },
  components:{FontAwesomeIcon},
  watch: {
    localCart: {
      handler: 'calculateTotals',
      deep: true
    }
  },
  methods:{
    kop(){
     const x= this.a.replace('/\\D/gm','')
      if(x.length===4 ||x.length===9 || x.length==14){
        this.a=x+' '
      }
      this.a = this.a.slice(0, 19);

    },
    modd() {
      // Remove non-numeric characters
      const numericValue = this.mod.replace(/\D/g, '');

      // Format the expiration date as MM/YY
      if (numericValue.length >= 2) {
        const month = numericValue.slice(0, 2);
        const year = numericValue.slice(2, 4);
        this.mod = `${month}/${year}`;
      } else {
        this.mod = numericValue;
      }

      // Limit the total length to 5 characters (including '/')
      if (this.mod.length > 5) {
        this.mod = this.mod.slice(0, 5);
      }
    },

      limit() {
        const maxLength = 3;
        this.inp = this.inp.slice(0, maxLength);
    },
    calculateTotals() {
      this.subTotal = this.localCart.reduce((total, item) => {
        if (!isNaN(item.total) && !isNaN(item.quantity)) {
          return total + item.total;
        }
        return total;
      }, 0);
      this.grandTotal = this.subTotal + 5 + 2.90 + 20;
    },
    removeItem(id) {
      console.log(id)
      this.localCart.forEach((item)=>{

        if(item.quantity === 1 && item.id===id) {
          this.localCart = this.localCart.filter(item => item.id !== id);
        }
        else if(item.id===id && item.quantity>=1){
          item.quantity-=1
          item.total-=item.productPrice
        }
      })
      
      localStorage.setItem('key',JSON.stringify(this.localCart))
      if(this.localCart.length===0){
        localStorage.removeItem('key')
      }
    },
    handleEnd(){
      localStorage.removeItem('key')
      this.ok=true
      setTimeout(()=>{
        this.ok=false
      },2500)
    }
  },


}
</script>

<style scoped>

</style>